@import url('https://fonts.googleapis.com/css2?family=Jacquard+24&family=Silkscreen:wght@400;700&display=swap');

.landing-page {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: black;
    overflow: hidden;
    font-family: "Jacquard 24", system-ui;
    font-weight: 400;
    font-style: normal;
    color: white;
    font-size: 46px;             /* Adjust font size */
}

/* Style for the input box */
input[name="myInput"] {
    background-color: black;     /* Black background */
    display: block;
    margin-left: auto;
    margin-right: auto;
    color: white;                /* White text */
    font-family: "Jacquard 24", system-ui;
    border: 1px solid #d3d3d3;   /* Faint gray outline */
    border-radius: 5px;          /* Slight rounded corners */
    font-size: 26px;             /* Adjust font size */
    outline: none;               /* Remove default focus outline */
}

/* Add focus effect for the input box */
input[name="myInput"]:focus {
    border-color: white;       /* Slightly darker gray outline on focus */
    box-shadow: 0 0 5px #a9a9a9; /* Subtle glow effect on focus */
}

.fire-image {
  display: block; /* Ensures it is a block element for spacing */
  margin: 0 auto; /* Centers the image horizontally */
  margin-bottom: 60px;
  width: 200px; /* Adjust as needed for your design */
  height: auto; /* Maintains aspect ratio */
}

.content {
  margin-top: 60px; /* Adds spacing below the image */
}

.response {
  margin-top: 60px; /* Adds spacing below the image */
}

.welcome-title {
    width: 700px;
    text-align: center;
    font-weight: 10;
}
