.main-page {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: black;
    overflow: hidden;
    color: white;
    font-size: 4rem;
    font-family: "Jacquard 24", system-ui;
    font-weight: 400;
    font-style: normal;
    transition: all 0.5s ease-in-out; /* Smooth layout change */

    -webkit-user-select: none; /* Safari */        
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* IE10+/Edge */
    user-select: none; /* Standard */
}

.message {
    display: block;
    margin: 0 auto;
    margin-top: 20px;
    width: 500px;
    text-align: center;
}

.text {
    transition: all 0.5s ease-in-out;
}

.text.move-down {
    transform: translateY(150px);
}

.top-image {
  display: block; /* Ensures it is a block element for spacing */
  margin: 0 auto; /* Centers the image horizontally */
  width: 400px; /* Adjust as needed for your design */
  height: auto; /* Maintains aspect ratio */
}

.content {
  margin-top: 20px; /* Adds spacing below the image */
}

.click-counter {
    font-size: 10rem;
    text-align: center;
}

@keyframes colorGlowLoop {
  0% {
    color: red;
    text-shadow: 0 0 5px red, 0 0 10px purple;
  }
  25% {
    color: orange;
    text-shadow: 0 0 5px yellow, 0 0 10px blue;
  }
  50% {
    color: white;
    text-shadow: 0 0 5px green, 0 0 10px green;
  }
  75% {
    color: coral;
    text-shadow: 0 0 5px blue, 0 0 10px blue;
  }
  100% {
    color: red;
    text-shadow: 0 0 5px purple, 0 0 10px purple;
  }
}

.looping-text {
  animation: colorGlowLoop 5s infinite ease-in-out;
}

