.main {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column; /* Align text and button vertically */
    background-color: black;
    overflow: hidden;
    font-family: "Jacquard 24", system-ui;
    font-weight: 400;
    font-style: normal;
    color: white;
    font-size: 46px;
    text-align: center; /* Centers text content */
}

.timer-container {
    display: flex;
    justify-content: flex-start; /* Aligns the image to the left */
    align-items: center;
    margin-bottom: 20px; /* Space between text+image and button */
}

.timer-text {
    width: 500px;
    height: 300px;
    flex-grow: 1;
}

.timer-image {
    display: block;
    width: 400px;
    height: 300px;
}

.gifs {
    height: 300px;
    width: auto;
}

.timer-button {
    font-family: "Jacquard 24", system-ui;
    font-size: 30px;
    background-color: black;
    color: white;
    height: 50px;
    width: 100px;
    border: 1px solid grey;
    margin-top: 20px; /* Space between image/text and the button */
}

.timer-button:hover {
    background-color: grey;
}

